<template>
    <div v-if="action">
        <popup :show="popups.partnerCouponsRemove.show" :name="popups.partnerCouponsRemove.name">
            <div>
                <div class="popup__header mb">
                    <div class="title"> ?</div>
                </div>


                <div class="popup__content">
                    <p>           .<br>
                              .     ,  ...</p>

                    <div class="row-b mt32">
                        <submit-btn tag="button"
                                    class="btn"
                                    @click.native="closePartnerCouponsRemove()"
                                    :type="buttons.delete.state">
                            
                        </submit-btn>
                    </div>
                </div>
            </div>
        </popup>

        <popup :show="popups.partnerModerateErrorField.show" :name="popups.partnerModerateErrorField.name">
            <div>
                <div class="popup__header mb">
                    <div class="title error">!</div>
                </div>


                <div class="popup__content">
                    <p>     .<br>
                                .</p>

                    <div class="row-b mt32">
                        <button class="btn" @click="closePartnerModerateErrorField()"> </button>
                    </div>
                </div>
            </div>
        </popup>

        <popup :show="popups.partnerModerateErrorPercent.show" :name="popups.partnerModerateErrorPercent.name">
            <div>
                <div class="popup__header mb">
                    <div class="title">   !</div>
                </div>


                <div class="popup__content">
                    <p>          .<br>
                            % (%    3%    )<br>
                               <br>
                                  .</p>

                    <div class="row-b mt32">
                        <button class="btn" @click="closePartnerModerateErrorPercent()">   </button>
                    </div>
                </div>
            </div>
        </popup>

        <popup :show="popups.partnerModerateErrorSum.show" :name="popups.partnerModerateErrorSum.name">
            <div>
                <div class="popup__header mb">
                    <div class="title">     !</div>
                </div>


                <div class="popup__content">
                    <p>          .<br>
                            % (%    3%    )<br>
                                   .</p>

                    <div class="row-b mt32">
                        <button class="btn" @click="closePartnerModerateErrorSum()">   </button>
                    </div>
                </div>
            </div>
        </popup>

        <popup :show="popups.partnerModerateErrorProvider.show" :name="popups.partnerModerateErrorProvider.name">
            <div>
                <div class="popup__header mb">
                    <div class="title error">      </div>
                </div>


                <div class="popup__content">
                    <p>      ,        !<br>
                                  !         ...</p>

                    <div class="row-b mt32">
                        <button class="btn" @click="closePartnerModerateErrorProvider()">    </button>
                    </div>
                </div>
            </div>
        </popup>


        <popup :show="popups.partnerCouponsEdit.show" :name="popups.partnerCouponsEdit.name">
            <div>
                <div class="popup__header mb">
                    <div class="title"> ?</div>
                </div>


                <div class="popup__content">
                    <p>      <br>
                               .</p>

                    <div class="row-b mt32">
                        <button class="btn" @click="closePartnerCouponsEdit()"></button>
                    </div>
                </div>
            </div>
        </popup>


        <popup :show="popups.partnerCouponsEditStopped.show" :name="popups.partnerCouponsEditStopped.name">
            <div>
                <div class="popup__header mb">
                    <div class="title"> ?</div>
                </div>


                <div class="popup__content">
                    <p>        .
                               
                             .</p>

                    <div class="row-b mt32">
                        <button class="btn" @click="closePartnerCouponsEditStopped()"></button>
                    </div>
                </div>
            </div>
        </popup>
        <popup :is-btn-close="false" :show="popups.partnerCouponsCancel.show" :name="popups.partnerCouponsCancel.name">
            <div class="popup__header mb">
                <div class="title"> ?</div>
            </div>
            <div class="popup__content">
                <p>         , <br>     </p>
                <div class="row-b mt32">
                    <submit-btn tag="button"
                                class="btn"
                                @click.native="closePartnerCouponsCancel()"
                                :type="buttons.cancel.state">
                        
                    </submit-btn>
                    <a href="javascript:void(0);" @click="close(popups.partnerCouponsCancel.name)" class="pseudo-link-cancel"></a>
                </div>
            </div>

        </popup>

        <popup :show="popups.partnerCouponsStop.show" :name="popups.partnerCouponsStop.name">
            <div>
                <div class="popup__header mb">
                    <div class="title"> ?</div>
                </div>


                <div class="popup__content">
                    <p>   ,        .</p>
                    <p>       .     
                            .</p>
                    <p> ,       {{ declOfNumber(action.statistics.valid,['','','']) }} <strong>{{ action.statistics.valid }} {{ declOfNumber(action.statistics.valid,['','','']) }}</strong>   
                            .</p>

                    <div class="row-b mt32">
                        <submit-btn tag="button"
                                    class="btn"
                                    @click.native="closePartnerCouponsStop()"
                                    :type="buttons.stop.state">
                            
                        </submit-btn>
                    </div>
                </div>
            </div>
        </popup>

        <popup :is-btn-close="false" :show="popups.partnerModerateConfirm.show" :name="popups.partnerModerateConfirm.name">
            <div>
                <div class="popup__header mb">
                    <div class="title">, !</div>
                </div>


                <div class="popup__content">
                    <div class="mt16"><input :class="{error: confirmError&&(confirmCheckbox.indexOf('1')==-1)}" v-model="confirmCheckbox" value="1" type="checkbox" id="confirm_1"/><label for="confirm_1"> ,      </label></div>
                    <div class="mt16"><input :class="{error: confirmError&&(confirmCheckbox.indexOf('2')==-1)}" v-model="confirmCheckbox" value="2" type="checkbox" id="confirm_2"/><label for="confirm_2"> ,      ,     </label></div>
                    <div class="mt16"><input :class="{error: confirmError&&(confirmCheckbox.indexOf('3')==-1)}" v-model="confirmCheckbox" value="3" type="checkbox" id="confirm_3"/><label for="confirm_3">  ,         </label></div>
                    <div class="mt16"><input :class="{error: confirmError&&(confirmCheckbox.indexOf('4')==-1)}" v-model="confirmCheckbox" value="4" type="checkbox" id="confirm_4"/><label for="confirm_4">     <a target="_blank" href="/partner/rules/"> </a></label></div>



                    <div class="row-b mt32">
                        <submit-btn tag="button"
                                    class="btn"
                                    @click.native="partnerModerateConfirm()"
                                    :type="buttons.moderate.state">
                            
                        </submit-btn>
                    </div>
                </div>
            </div>
        </popup>

        <popup :is-btn-close="false" :show="popups.actionToModaretionConfirm.show" :name="popups.actionToModaretionConfirm.name">
                <action-to-modaration-confirm :action="action"></action-to-modaration-confirm>
        </popup>

        <popup :is-btn-close="false" :show="popups.actionToModaretionConfirmRepeat.show" :name="popups.actionToModaretionConfirmRepeat.name">
            <div class="popup-info">
                <div>
                    <img src="@/assets/img/icon-ok-big.svg" alt="">
                </div>

                <div class="text mt32">
                    <div class="text-size-xxl text-color-green text-bolder">   !</div>

                    <p class="mt24">
                             ,<br>      {{action.lk_status_publicate_date}}
                    </p>
                </div>

                <div class="mt32">
                    <button class="btn btn--white-green" @click="closeActionToModaretionConfirmRepeat()"> !</button>
                </div>
            </div>
        </popup>

        <popup isSetBlank="true" :width="752" :show="popups.partnerCouponsStat.show"
               :name="popups.partnerCouponsStat.name">
            <partner-coupons-stat :action="action"></partner-coupons-stat>
        </popup>
    </div>
</template>

<script>
    import Popup from '@/components/modal/Popup.vue';
    import {mapState} from 'vuex';
    import PartnerCouponsStat from '@/components/modal/PartnerCouponsStat.vue';
    import ActionToModarationConfirm from '@/components/modal/ActionToModarationConfirm.vue';
    import SubmitBtn from '@/components/forms/SubmitBtn.vue'
    import {declOfNumber} from '@/helpers/formatted';

    export default {
        name: "ActionModals",
        components: {
            Popup,
            PartnerCouponsStat,
            SubmitBtn,
            ActionToModarationConfirm
        },
        props: {
            action: {}
        },
        data() {
            return {
                buttons: {
                    copy: {
                        state: 'normal'
                    },
                    stop: {
                        state: 'normal'
                    },
                    active: {
                        state: 'normal'
                    },
                    archive: {
                        state: 'normal'
                    },
                    delete: {
                        state: 'normal'
                    },
                    moderate:{
                        state: 'normal'
                    },
                    cancel:{
                        state: 'normal',
                    }
                },
                confirmCheckbox:[],
                confirmError: false,
            }
        },
        computed: {
            ...mapState({
                popups: state => state.popups.items,
                statuses: state => state.partnerActions.statuses,
            }),
            currentStatus() {
                return this.$route.params.status ? this.$route.params.status : this.defaultStatus
            },
        },
        methods: {
            close: function (name) {
                this.$store.commit('popups/close', {name: name});
            },
            closePartnerCouponsEdit: function () {
                this.$store.commit('popups/close', {name: this.popups.partnerCouponsEdit.name});
                this.$router.push('/partner/coupon-edit/id'+this.action.id);
            },
            closeActionToModaretionConfirmRepeat: function(){
                this.$store.commit('popups/close', {name: this.popups.actionToModaretionConfirmRepeat.name});
            },
            closePartnerCouponsCancel: function () {
                this.$store.commit('partnerActions/setButtonState',{id:this.action.id, button: 'cancel', state: 'load'});
                this.buttons.cancel.state = 'load';

                this.$store.dispatch('partnerActions/setStatus',{id:this.action.id,status:this.statuses.develop}).then(() => {
                    this.$store.commit('partnerActions/setButtonState',{id:this.action.id, button: 'cancel', state: 'ok'});
                    this.buttons.cancel.state = 'ok';
                    if(this.$route.name !== 'actions') {
                        setTimeout(()=>{
                            this.$store.commit('popups/close', {name: this.popups.partnerCouponsCancel.name});
                            this.$store.dispatch('partnerActions/initDetailPage', {route: this.$route});
                            this.buttons.cancel.state = 'normal';
                        },300);
                    } else {
                        this.$router.push('/partner/coupons/status'+this.statuses.develop);
                        setTimeout(()=>{
                            this.$store.commit('popups/close', {name: this.popups.partnerCouponsCancel.name});
                            this.buttons.cancel.state = 'normal';
                        },300);
                    }
                });
            },
            closePartnerCouponsStop: function () {
                this.buttons.stop.state = 'load';
                //this.$store.commit('partnerActions/setButtonState',{id:this.action.id, button: 'archive', state: 'load'});
                this.$store.dispatch('partnerActions/setStatus',{id:this.action.id,status:this.statuses.archive}).then(() => {
                    this.buttons.stop.state = 'ok';
                    //this.$store.commit('partnerActions/setButtonState',{id:this.action.id, button: 'archive', state: 'ok'});
                    if(this.$route.name !== 'actions') {
                        this.$store.commit('popups/close', {name: this.popups.partnerCouponsStop.name});
                        this.$store.dispatch('partnerActions/initDetailPage', {route: this.$route});
                        this.buttons.stop.state = 'normal';
                    }
                    else{
                        setTimeout(()=>{
                            this.buttons.stop.state = 'normal';
                            this.$store.commit('partnerActions/setButtonState',{id:this.action.id, button: 'archive', state: 'normal'});
                            this.$router.push('/partner/coupons/status'+this.statuses.archive);
                            this.$store.commit('popups/close', {name: this.popups.partnerCouponsStop.name});
                        }, 300);
                    }
                });
            },
            closePartnerModerateErrorField: function () {
                this.$router.push('/partner/coupon-edit/id'+this.action.id);
                this.$store.commit('popups/close', {name: this.popups.partnerModerateErrorField.name});
            },
            closePartnerModerateErrorPercent: function () {
                this.$router.push('/partner/coupon-edit/id'+this.action.id);
                this.$store.commit('popups/close', {name: this.popups.partnerModerateErrorPercent.name});
            },
            closePartnerModerateErrorSum: function () {
                this.$router.push('/partner/coupon-edit/id'+this.action.id);
                this.$store.commit('popups/close', {name: this.popups.partnerModerateErrorSum.name});
            },
            closePartnerModerateErrorProvider: function () {
                this.$store.commit('popups/close', {name: this.popups.partnerModerateErrorProvider.name});
                this.$router.push('/partner/about-edit');
            },
            closePartnerCouponsEditStopped: function () {
                this.$store.commit('popups/close', {name: this.popups.partnerCouponsEditStopped.name});
                this.$router.push('/partner/coupon-edit/id'+this.action.id);
            },
            closePartnerCouponsRemove: function () {
                this.$store.commit('partnerActions/setButtonState',{id:this.action.id, button: 'delete', state: 'load'});
                this.buttons.delete.state = 'load';
                this.$store.dispatch('partnerActions/setStatus',{id:this.action.id,status:this.statuses.deleted}).then(() => {
                    this.$store.commit('partnerActions/setButtonState',{id:this.action.id, button: 'delete', state: 'ok'});
                    this.buttons.delete.state = 'ok';
                    if(this.$route.name !== 'actions') {
                        this.$store.dispatch('partnerActions/initDetailPage', {route: this.$route});
                        this.$store.commit('popups/close', {name: this.popups.partnerCouponsRemove.name});
                    } else {
                        this.$store.dispatch('partnerActions/getItems',{status:this.currentStatus,noClear: true}).then(()=>{
                            this.$store.commit('partnerActions/setButtonState',{id:this.action.id, button: 'delete', state: 'normal'});
                            this.$store.commit('popups/close', {name: this.popups.partnerCouponsRemove.name});
                            setTimeout(()=>{
                                this.buttons.delete.state = 'normal';
                            }, 300);
                        })
                    }
                });
            },
            partnerModerateConfirm: function() {
                this.confirmError = false;
                if(this.confirmCheckbox.length == 4){
                    this.buttons.moderate.state = 'load';
                    this.$store.dispatch('partnerActions/setStatus',{id:this.action.id,status:this.statuses.active}).then(() => {
                        this.buttons.moderate.state = 'ok';
                        setTimeout(()=>{
                            this.buttons.moderate.state = 'normal';
                            this.$store.dispatch('popups/open', {name: this.popups.actionToModaretionConfirm.name});
                            this.$store.commit('popups/close', {name: this.popups.partnerModerateConfirm.name});
                            if(this.$route.name !== 'actions') {
                                this.$store.dispatch('partnerActions/initDetailPage', {route: this.$route});
                            }
                            else{
                                this.$router.push('/partner/coupons/status'+this.statuses.active);
                            }
                            this.confirmCheckbox = [];
                        }, 300);
                    });
                }
                else{
                    this.confirmError = true;
                }
            },

            declOfNumber(number, titles){
                return declOfNumber(number, titles);
            },
        }
    }
</script>

<style>

</style>
