                                    <span class="item__control">
                                        <span class="icon-btn i i-pencil2" @click="openConstructActionGoLink"></span>
                                        <span class="icon-btn i i-trash" style="transform: scale(0.8)" @click="goLinkCheckBox = false"></span>
                                    </span>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
                <!-- end wrapper-content -->

                <div v-if="stepsCreateAction.five.show" class="wrapper-content wrapper-content-inner mt40 pt32 pb32"
                     style="position: relative; ">
                    <div class="h1" id="coupon-cities">  </div>

                    <a v-if="!(stepsCreateAction.five.fields.cities.value.length > 0 && stepsCreateAction.five.fields.cities.value[0].xmlId === 'all')"
                       class="construct-city-all" href="javascript:void(0);" @click="openConstructActionAllCity">
                            </a>

                    <div class="mt32">
                        <div class="tags tags--edit">
                            <div class="tags-wrap">

                                <span class="tag" v-for="city in stepsCreateAction.five.fields.cities.value">
                                    <span class="text">{{ city.name }}</span>
                                    <span
                                          class="del" @click="delCity(city.xmlId)"></span>
                                </span>

                                <span v-if="stepsCreateAction.five.fields.cities.value.length === 0 || (stepsCreateAction.five.fields.cities.value.length > 0 && stepsCreateAction.five.fields.cities.value[0].xmlId !== 'all')" @click="openConstructActionCity" class="tag tag--add" :class="{error:stepsCreateAction.five.fields.cities.error}">
                                    <span class="icon">+</span>
                                    <span class="text"> </span>
                                </span>
                            </div>
                            <!-- end tags-wrap -->
                        </div>
                        <!-- end tags -->
                    </div>
                </div>
                <!-- end wrapper-content -->

                <div v-if="stepsCreateAction.finish.show" class="wrapper-content wrapper-content-inner mt40 pt32 pb32"
                     style="position: relative; ">
                    <div class="h1" id="coupon-tags">  </div>


                    <div class="row-b mt32">
                        <div class="row-b__col elem-pos-fit mr24">
                            <tag-editor
                                    :error="stepsCreateAction.finish.fields.tags.error"
                                    @focus="fieldFocus('tags','finish')"
                                    @blur="fieldBlur('tags','finish')"
                                    v-model="tags"></tag-editor><!--  -->
                        </div>

                        <div class="row-b__col elem-pos-fix"
                             :class="{'--active':stepsCreateAction.finish.fields.tags.focus}"
                             style="width: 248px; min-width: 248px; ">
                            <div class="mtb construct-form__big-note">
                                <div class="construct-form__note mt16">  ( ),   <br>
                                        
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
                <!-- end wrapper-content -->

                <div class="row-b" v-if="currentStep !== 'finish'" :style="styleBtnNext">
                    <div class="row-b__col elem-pos-fix mr24">
                        <button class="btn" @click="nextStep()"></button>
                    </div>

                    <div class="row-b__col elem-pos-fit" v-if="stepsCreateAction.one.isCheckStep">
                        <div>
                             ,      , <br>
                                  ""
                        </div>
                    </div>
                </div>
                <div v-else class="row-b" style="margin: 40px 24px 0;">
                    <div class="row-b__col elem-pos-fix mr24">
                        <submit-btn tag="button"
                                    class="btn btn--green"
                                    @click.native="nextStep"
                                    :type="buttons.save.state">
                            
                        </submit-btn>
                    </div>

                    <div class="row-b__col elem-pos-fit">
                        <div>
                              ,        .
                        </div>
                    </div>
                </div>

            </div>
            <!-- end container__full -->

        </section>
        <!-- end container -->


        <!--    (  ?) -->
        <popup :width="744" :show="popups.originDiscount.show" :name="popups.originDiscount.name">
            <origin-discount :selected-prop="stepsCreateAction.one.fields.originDiscount.value"
                             @change="originDiscountChange"></origin-discount>
        </popup>


        <!--    -->
        <popup :show="popups.confirmDiscount.show" :name="popups.confirmDiscount.name">
            <confirm-discount @change="confirmDiscountChange"></confirm-discount>
        </popup>


        <!--    (1- ) -->
        <popup :show="popups.correctText.show" :name="popups.correctText.name">
            <div>
                <div class="popup__content">
                    <div class="h1 mb8">
                          
                    </div>

                    <div class="text-bold">
                             ?
                    </div>

                    <div class="mt24" v-html="onenessDiscountHtml">
                    </div>


                    <div class="row-b mt32">
                        <button class="btn mr32" @click="hideConfirmDiscount"> !</button>
                        <a href="javascript:void(0)" @click="prevConfirmDiscount">  </a>
                    </div>
                </div>
            </div>
        </popup>


        <!--    (2- ) -->
        <popup :show="popups.correctTextNoDiscount.show" :name="popups.correctTextNoDiscount.name">
            <div>
                <div class="popup__content">
                    <div class="h1 mb8">
                          
                    </div>

                    <div class="text-bold">
                             ?
                    </div>

                    <div class="mt24" v-html="onenessDiscountHtml"></div>


                    <div class="row-b mt32">
                        <button class="btn mr32" @click="hideConfirmDiscount"> !</button>
                        <a href="javascript:void(0)" @click="prevConfirmDiscount">  </a>
                    </div>
                </div>
            </div>
        </popup>

        <!--  ... -->
        <popup :show="popups.constructActionCallUs.show" :name="popups.constructActionCallUs.name"  :add-class="'popup-choose-action'">
            <construct-action-call-us
                    :phone-prop="stepsCreateAction.four.fields.callUs.value.phone ? stepsCreateAction.four.fields.callUs.value.phone : ''"
                    :operation-prop="stepsCreateAction.four.fields.callUs.value.operation ? stepsCreateAction.four.fields.callUs.value.operation : ''"
                    :target-prop="stepsCreateAction.four.fields.callUs.value.target ? stepsCreateAction.four.fields.callUs.value.target : ''"
                    @save="setCallUs"></construct-action-call-us>
        </popup>

        <!--   ... -->
        <popup :show="popups.constructActionGoLink.show" :name="popups.constructActionGoLink.name" :add-class="'popup-choose-action'">
            <construct-action-link
                    :link-prop="stepsCreateAction.four.fields.goLink.value.link ? stepsCreateAction.four.fields.goLink.value.link : ''"
                    @save="setGoLink"></construct-action-link>
        </popup>

        <!--   ... -->
        <popup  ref="constructActionComeUs" :show="popups.constructActionComeUs.show" :name="popups.constructActionComeUs.name" :add-class="'popup-choose-action'">
            <construct-action-come
                    @resize="$refs.constructActionComeUs.updateViewportSize()"
                    :fields="stepsCreateAction.four.fields.comeUs.value"
                    @save="setComeUs"></construct-action-come>
        </popup>

        <!--  ,         ? -->
        <popup :is-btn-close="false" :show="popups.constructActionAllCity.show"
               :name="popups.constructActionAllCity.name">
            <construct-action-all-city></construct-action-all-city>
        </popup>

        <city-choice :regions-prop="citySelectOptions"
                     :show="popups.constructActionCity.show"
                     :name="popups.constructActionCity.name"
                     @change="cityChange"></city-choice>
        <!--      -->
        <popup :width="992" :show="popups.constructActionVideo.show" :name="popups.constructActionVideo.name"
               :add-class="'popup--video'">
            <iframe width="992"
                    height="548"
                    src="https://www.youtube.com/embed/WBaKEl5HIzU"
                    frameborder="0"
                    allow="accelerometer; autoplay; encrypted-media; gyroscope; picture-in-picture"
                    allowfullscreen></iframe>
        </popup>

        <popup :width="720" :is-btn-close="false" :show="popups.partnerModerateErrorPercent.show" :name="popups.partnerModerateErrorPercent.name">
            <div>
                <div class="popup__header mb">
                    <div class="title">   </div>
                </div>


                <div class="popup__content">
                    <b>    ?</b>
                    <ul class="ul-disc-small mt0">
                        <li>      {{ options.minDiscount }}%,   {{ options.ourDiscount }}%      .</li>
                        <li>      .  {{ options.minDiscount }}%    .</li>
                    </ul>

                    <div class="row-b mt32">
                        <button class="btn btn--white-red" @click="closePartnerModerateErrorPercent()">   </button>
                    </div>
                </div>
            </div>
        </popup>

        <popup :width="720" :is-btn-close="false" :show="popups.partnerModerateErrorSum.show" :name="popups.partnerModerateErrorSum.name">
            <div>
                <div class="popup__header mb">
                    <div class="title">     !</div>
                </div>


                <div class="popup__content">
                    <p>         {{ number_format(averageCheck) }} .<br>
                            {{ discount - options.ourDiscount }}% ({{ discount - options.ourDiscount }}%    {{ options.ourDiscount }}%    )<br>
                              {{ economy }}     .</p>

                    <div class="row-b mt32">
                        <button class="btn" @click="closePartnerModerateErrorSum()">   </button>
                        <a href="javascript:void(0);" @click="close(popups.partnerModerateErrorSum.name)" class="pseudo-link-cancel"></a>
                    </div>
                </div>
            </div>
        </popup>

        <popup :is-btn-close="false" :show="popups.cooperationCancelSmallPriceOne.show" :name="popups.cooperationCancelSmallPriceOne.name" :width="808">
            <cooperation-cancel-small-price-one
                    @close="setFocus('projectDiscount')"
                    :average="averageCheck"
                    :discount="discountUser"
                    :projectDiscount="discount"
            ></cooperation-cancel-small-price-one>
        </popup>

        <!--   ? -->
        <popup :is-btn-close="false" :show="popups.constructHowUseVars.show" :name="popups.constructHowUseVars.name">
            <construct-how-use-vars></construct-how-use-vars>
        </popup>
        <popup v-if="stepsCreateAction.four.fields.comeUsCheckBox.value"
               :show="popups.address.show"
               :name="popups.address.name"
               :add-class="'popup--map'"
               :is-set-blank="true"
        >
            <Map :markers="getMarkers()"
                 :height="558"
                 :coords="mapCenter"
                 :zoom="11"
            ></Map>
        </popup>
    </div>
</template>

<script>
    import {mapState} from 'vuex'
    import {scrollTo} from '@/helpers/animate.js';
    import Popup from '@/components/modal/Popup.vue';
    import Tooltip from '@/components/Tooltip.vue';
    import InputStyled from '@/components/forms/InputStyled.vue';
    import TextEditorMax from '@/components/editor/TextEditorMax.vue';
    import FileUploadCropper from "@/components/forms/FileUploadCropper.vue";
    import FileUploadCropperGallery from "@/components/forms/FileUploadCropperGallery.vue";
    import OriginDiscount from '@/components/modal/OriginDiscount.vue'
    import ConfirmDiscount from '@/components/modal/ConfirmDiscount.vue'
    import InputPrice from "@/components/forms/InputPrice";
    import {number_format} from '@/helpers/formatted.js';
    import ConstructActionCallUs from '@/components/modal/ConstructActionCallUs.vue'
    import ConstructActionCome from '@/components/modal/ConstructActionCome.vue'
    import ConstructActionAllCity from '@/components/modal/ConstructActionAllCity.vue'
    import TagEditor from '@/components/forms/TagEditor.vue'
    import CityChoice from '@/components/forms/CityChoice.vue'
    import SubmitBtn from '@/components/forms/SubmitBtn.vue'
    import ConstructCheckShow from '@/components/ConstructCheckShow.vue'
    import TextareaField from '@/components/forms/TextareaField.vue'
    import VideoGalleryAdd from '@/components/forms/VideoGalleryAdd.vue'
    import {actionDiscountHtml} from '@/helpers/tempateHtml';
    import CooperationCancelSmallPriceOne from '@/components/modal/CooperationCancelSmallPriceOne.vue';
    import LabelHint from '@/components/forms/LabelHint.vue'
    import ConstructHowUseVars from '@/components/modal/ConstructHowUseVars.vue';
    import ConstructActionLink from '@/components/modal/ConstructActionLink.vue';
    import Map from '@/components/Map.vue'
    import FileUploadCropperGalleryMulti from "@/components/forms/FileUploadCropperGalleryMulti.vue";

    export default {
        name: "ActionEditor",
        components: {
            FileUploadCropperGalleryMulti,
            InputPrice,
            Popup,
            Tooltip,
            InputStyled,
            TextEditorMax,
            FileUploadCropper,
            FileUploadCropperGallery,
            OriginDiscount,
            ConfirmDiscount,
            ConstructActionCallUs,
            ConstructActionCome,
            ConstructActionAllCity,
            TagEditor,
            CityChoice,
            SubmitBtn,
            ConstructCheckShow,
            TextareaField,
            VideoGalleryAdd,
            CooperationCancelSmallPriceOne,
            LabelHint,
            Map,
            ConstructHowUseVars,
            ConstructActionLink,
        },
        props: {
            isModerator: {
                default: false
            }
        },
        data() {
            return {
                mapCenter: [55.427862,37.550162],
                cityPartner: {},
                isAllCities: false,
                cityAll: {},
                prevCitySelect: {},
                citySelectOptions: {},
                buttons: {
                    save: {
                        state: 'normal'
                    },
                },
            }
        },
        mounted() {
            if (this.partner.city) {
                this.cityPartner = this.getCityByXmlId(this.partner.city);
            } else {
                this.cityPartner = this.currentCityIp;
            }
            if (this.$route.params.id === 'new') {

                this.$store.commit('partnerActions/setFormCreateField', {
                    name: 'cities',
                    value: [this.cityPartner],
                    step: 'five',
                })
                for (let i in this.stepsCreateAction) {
                    this.$store.commit('partnerActions/showStep', {
                        value: false,
                        step: i,
                    })
                }
                this.$store.commit('partnerActions/setCurrentStep', 'one');

            } else {
                if (this.stepsCreateAction.one.fields.originDiscount.value.length === 0) {
                    this.$store.commit('partnerActions/setCurrentStep', 'one');
                } else {
                    for (let i in this.stepsCreateAction) {
                        this.$store.commit('partnerActions/showStep', {
                            value: true,
                            step: i,
                        })
                    }
                    this.$store.commit('partnerActions/setCurrentStep', 'finish')
                }
            }
            this.changeCitySelect();

        },
        computed: {
            ...mapState({
                popups: state => state.popups.items,
                user: state => state.users.user,
                cities: state => state.cities.items,
                masks: state => state.masks.items,
                currentStep: state => state.partnerActions.currentStep,
                tmpId: state => state.partnerActions.tmpId,
                stepsCreateAction: state => state.partnerActions.stepsCreateAction,
                templates: state => state.partnerActions.templates,
                detail: state => state.partnerActions.detail,
                currentCityIp: state => state.cities.currentCityIp,
                options: state => state.options.items,
                statuses: state => state.partnerActions.statuses,
            }),
            statistics() {
                let stat = {};

                if (typeof (this.detail.statistics) === 'undefined') {
                    stat = {
                        sold: 0,
                        ext: 0
                    };
                } else {
                    stat = this.detail.statistics;
                }

                return stat;
            },
            countHowToUse(){
                let i = 0;
                if(this.stepsCreateAction.four.fields.callUsCheckBox.value && this.stepsCreateAction.four.fields.callUs.value){
                    i++;
                }
                if(this.stepsCreateAction.four.fields.comeUsCheckBox.value && this.stepsCreateAction.four.fields.comeUs.value){
                    i++;
                }
                if(this.stepsCreateAction.four.fields.goLinkCheckBox.value && this.stepsCreateAction.four.fields.goLink.value){
                    i++;
                }
                return i;
            },
            couponAverageDisabled(){
                if(this.detail.statistics){
                    return (this.detail.statistics.ext<2)?false:true;
                }
                else{
                    return false;
                }
            },
            styleBtnNext() {
                return this.currentStep === 'two' ? 'margin: 40px 0 0;' : 'margin: 40px 24px 0;';
            },
            partner() {
                if (this.$store.state.partner && this.$store.state.partner.current.id) {
                    return this.$store.state.partner.current
                } else if (this.$store.state.partnerActions.detail.partner) {
                    return this.$store.state.partnerActions.detail.partner
                } else {
                    return {}
                }
            },
            economy() {
                let result = 0;
                if (this.stepsCreateAction.one.fields.averageCheck.value && this.stepsCreateAction.one.fields.discount.value) {
                    let averageCheck = this.stepsCreateAction.one.fields.averageCheck.value.split(' ').join('');
                    result = averageCheck * (this.stepsCreateAction.one.fields.discount.value - this.options.ourDiscount) / 100;
                    result = number_format(result, 0, '', ' ')
                }
                return result;
            },
            /*image: {
                get() {
                    return this.stepsCreateAction.two.fields.image.value
                },
                set(value) {
                    this.$store.commit('partnerActions/setFormCreateField', {
                        name: 'image',
                        value: value,
                        step: 'two',
                    });
                }
            },*/
            discountUser: {
                get() {
                    return this.stepsCreateAction.one.fields.discountUser.value
                },
                set(value) {
                    this.$store.commit('partnerActions/setFormCreateField', {
                        name: 'discountUser',
                        value: value,
                        step: 'one',
                    });
                    if(+value === 0){
                        this.$store.commit('partnerActions/setFormCreateField', {
                            name: 'onenessDiscount',
                            value: '      , ' +
                            '  <br>     <strong>#DISCOUNT#%</strong>.<br>' +
                            '   ,  ZemSkidki.ru',
                            step: 'one',
                        })
                    } else {
                        this.$store.commit('partnerActions/setFormCreateField', {
                            name: 'onenessDiscount',
                            value: '       <strong>'+this.discountUser+'%</strong>, ' +
                            '  <br>     <strong>#DISCOUNT#%</strong>.<br>' +
                            '   ,  ZemSkidki.ru',
                            step: 'one',
                        })
                    }
                }
            },
            name: {
                get() {
                    return this.stepsCreateAction.one.fields.name.value
                },
                set(value) {
                    this.$store.commit('partnerActions/setFormCreateField', {
                        name: 'name',
                        value: value,
                        step: 'one',
                    })
                }
            },
            averageCheck: {
                get() {
                    return this.stepsCreateAction.one.fields.averageCheck.value
                },
                set(value) {
                    this.$store.commit('partnerActions/setFormCreateField', {
                        name: 'averageCheck',
                        value: value,
                        step: 'one',
                    })
                }
            },
            callUsCheckBox: {
                get() {
                    return this.stepsCreateAction.four.fields.callUsCheckBox.value
                },
                set(value) {
                    this.$store.commit('partnerActions/setFormCreateField', {
                        name: 'callUsCheckBox',
                        value: value,
                        step: 'four',
                    })
                    this.$store.commit('partnerActions/setFormCreateError', {
                        name: 'callUsCheckBox',
                        step: 'four',
                        value: false
                    })
                    this.$store.commit('partnerActions/setFormCreateError', {
                        name: 'comeUsCheckBox',
                        step: 'four',
                        value: false
                    })
                    this.$store.commit('partnerActions/setFormCreateError', {
                        name: 'goLinkCheckBox',
                        step: 'four',
                        value: false
                    })

                }
            },
            comeUsCheckBox: {
                get() {
                    return this.stepsCreateAction.four.fields.comeUsCheckBox.value
                },
                set(value) {
                    this.$store.commit('partnerActions/setFormCreateField', {
                        name: 'comeUsCheckBox',
                        value: value,
                        step: 'four',
                    })
                    this.$store.commit('partnerActions/setFormCreateError', {
                        name: 'callUsCheckBox',
                        step: 'four',
                        value: false
                    })
                    this.$store.commit('partnerActions/setFormCreateError', {
                        name: 'comeUsCheckBox',
                        step: 'four',
                        value: false
                    })
                    this.$store.commit('partnerActions/setFormCreateError', {
                        name: 'goLinkCheckBox',
                        step: 'four',
                        value: false
                    })

                }
            },
            goLinkCheckBox: {
                get() {
                    return this.stepsCreateAction.four.fields.goLinkCheckBox.value
                },
                set(value) {
                    this.$store.commit('partnerActions/setFormCreateField', {
                        name: 'goLinkCheckBox',
                        value: value,
                        step: 'four',
                    })
                    this.$store.commit('partnerActions/setFormCreateError', {
                        name: 'callUsCheckBox',
                        step: 'four',
                        value: false
                    })
                    this.$store.commit('partnerActions/setFormCreateError', {
                        name: 'comeUsCheckBox',
                        step: 'four',
                        value: false
                    })
                    this.$store.commit('partnerActions/setFormCreateError', {
                        name: 'goLinkCheckBox',
                        step: 'four',
                        value: false
                    })

                }
            },
            discount: {
                get() {
                    return this.stepsCreateAction.one.fields.discount.value
                },
                set(value) {
                    if (+value > 0) {
                        this.$store.commit('partnerActions/setFormCreateBtnState', {
                            name: 'origin',
                            value: 'normal',
                            step: 'one',
                        })
                        this.$store.commit('partnerActions/setFormCreateBtnState', {
                            name: 'oneness',
                            value: 'normal',
                            step: 'one',
                        })
                    } else {
                        this.$store.commit('partnerActions/setFormCreateBtnState', {
                            name: 'origin',
                            value: 'disable',
                            step: 'one',
                        })
                        this.$store.commit('partnerActions/setFormCreateBtnState', {
                            name: 'oneness',
                            value: 'disable',
                            step: 'one',
                        })
                    }
                    this.$store.commit('partnerActions/setFormCreateField', {
                        name: 'discount',
                        value: value,
                        step: 'one',
                    })
                }
            },
            originDiscountHtml(){
                return this.stepsCreateAction.one.fields.originDiscount.value ? actionDiscountHtml(this.stepsCreateAction.one.fields.originDiscount.value, this.stepsCreateAction.one.fields.discount.value - this.options.ourDiscount) : ''
            },
            onenessDiscountHtml(){
                return this.stepsCreateAction.one.fields.onenessDiscount.value ? actionDiscountHtml(this.stepsCreateAction.one.fields.onenessDiscount.value, this.stepsCreateAction.one.fields.discount.value - this.options.ourDiscount) : ''
            },
            tags: {
                get() {
                    return this.stepsCreateAction.finish.fields.tags.value
                },
                set(value) {
                    this.$store.commit('partnerActions/setFormCreateField', {
                        name: 'tags',
                        value: value,
                        step: 'finish',
                    })
                }
            },
            citiesAction: {
                get() {
                    return this.stepsCreateAction.five.fields.cities.value
                },
                set(value) {
                    this.$store.commit('partnerActions/setFormCreateField', {
                        name: 'cities',
                        value: value,
                        step: 'five',
                    })
                }
            },
            aboutActionVideo: {
                get() {
                    return this.stepsCreateAction.three.fields.aboutActionVideo.value
                },
                set(value) {
                    this.$store.commit('partnerActions/setFormCreateField', {
                        name: 'aboutActionVideo',
                        value: value,
                        step: 'three',
                    });
                    if(this.stepsCreateAction.three.fields.aboutActionVideo.error && value.length > 0){
                        this.$store.commit('partnerActions/setFormCreateError', {
                            name: 'aboutActionVideo',
                            value: false,
                            step: 'three',
                        });
                    }
                }
            },
            aboutActionPromo: {
                get() {
                    return this.stepsCreateAction.three.fields.aboutActionPromo.value
                },
                set(value) {
                    this.$store.commit('partnerActions/setFormCreateField', {
                        name: 'aboutActionPromo',
                        value: value,
                        step: 'three',
                    });
                    if(this.stepsCreateAction.three.fields.aboutActionPromo.error){
                        this.$store.commit('partnerActions/setFormCreateError', {
                            name: 'aboutActionPromo',
                            value: false,
                            step: 'three',
                        });
                    }
                }
            },
            aboutActionInclude: {
                get() {
                    return this.stepsCreateAction.three.fields.aboutActionInclude.value
                },
                set(value) {
                    this.$store.commit('partnerActions/setFormCreateField', {
                        name: 'aboutActionInclude',
                        value: value,
                        step: 'three',
                    })
                }
            },
            aboutActionExclude: {
                get() {
                    return this.stepsCreateAction.three.fields.aboutActionExclude.value
                },
                set(value) {
                    this.$store.commit('partnerActions/setFormCreateField', {
                        name: 'aboutActionExclude',
                        value: value,
                        step: 'three',
                    })
                }
            },
            aboutActionPrivileges: {
                get() {
                    return this.stepsCreateAction.three.fields.aboutActionPrivileges.value
