                    }
                }
            },
        },

        methods: {
            balloonTemplate(address) {
                let image = this.stepsCreateAction.two.fields.aboutActionPhoto.value.length > 0 ? this.stepsCreateAction.two.fields.aboutActionPhoto.value[0].icon : '';
                return `
                  <div class="yamap_baloon_link yamap_baloon_link_address hover">
                    <img width="80" src="//api.zemskidki.ru/` + image + `" alt="` + this.name + `">
                    ` + this.partner.name + `<br>
                    <b class="yamap_baloon_link-name">` + this.name + `</b><br/>
                    ` + address + `
                  </div>
                `
            },
            openMap: function(key) {
                let markers = {},
                    coords;

                if (this.stepsCreateAction.four.fields.comeUs.value.coord) {
                    coords = this.stepsCreateAction.four.fields.comeUs.value.coord.value ? this.stepsCreateAction.four.fields.comeUs.value.coord.value.split(',') : [];
                    markers[0] = [coords[1], coords[0]];
                }
                if(this.stepsCreateAction.four.fields.comeUs.value.otherAddresses){
                    for (let index in this.stepsCreateAction.four.fields.comeUs.value.otherAddresses) {
                        if (this.stepsCreateAction.four.fields.comeUs.value.otherAddresses[index].coord.value) {
                            let coords = this.stepsCreateAction.four.fields.comeUs.value.otherAddresses[index].coord.value.split(',');
                            markers[parseInt(index) + 1] = [coords[1], coords[0]];
                        }
                    }
                }


                if (markers[key]) {
                    this.mapCenter = markers[key];
                    this.$store.dispatch('popups/open', {name: this.popups.address.name});
                }

            },
            getMarkers: function () {
                let markers = [];

                if (this.stepsCreateAction.four.fields.comeUs.value.coord) {
                    let coords = this.stepsCreateAction.four.fields.comeUs.value.coord.value ? this.stepsCreateAction.four.fields.comeUs.value.coord.value.split(',') : [];

                    markers.push({
                        "id": 10,
                        "type": "placemark",
                        "coords": [coords[1], coords[0]],
                        "hint": "",
                        "balloon": {
                            "header": "",
                            "body": this.balloonTemplate(this.stepsCreateAction.four.fields.comeUs.value.address.value),
                            "footer": ""
                        },
                        "cluster": "1"
                    });


                }
                for (let index in this.stepsCreateAction.four.fields.comeUs.value.otherAddresses) {
                    if (this.stepsCreateAction.four.fields.comeUs.value.otherAddresses[index].coord.value) {
                        let coords = this.stepsCreateAction.four.fields.comeUs.value.otherAddresses[index].coord.value.split(',');
                        let obj = {
                            "id": parseInt(index) + 1,
                            "type": "placemark",
                            "coords": [coords[1], coords[0]],
                            "hint": "",
                            "balloon": {
                                "header": "",
                                "body": this.balloonTemplate(this.stepsCreateAction.four.fields.comeUs.value.otherAddresses[index].address.value),
                                "footer": ""
                            },
                            "cluster": "1"
                        };

                        markers.push(obj);
                    }
                }
                return markers;
            },
            openModalHowToUse() {
                this.$store.dispatch('popups/open', {name: this.popups.constructHowUseVars.name});
            },
            callUsCheckBoxClick() {
                this.$store.dispatch('popups/open', {name: this.popups.constructActionCallUs.name});
            },
            comeUsCheckBoxClick() {
                this.$store.dispatch('popups/open', {name: this.popups.constructActionComeUs.name});
            },
            scrollTo(element) {
                scrollTo(element);
            },
            setCallUs(value) {
                this.$store.commit('partnerActions/setFormCreateField', {
                    name: 'callUs',
                    value: value,
                    step: 'four',
                })
                this.$store.commit('partnerActions/setFormCreateField', {
                    name: 'callUsCheckBox',
                    value: true,
                    step: 'four',
                })
                this.$store.commit('partnerActions/setFormCreateError', {
                    name: 'callUsCheckBox',
                    step: 'four',
                    value: false
                })
                this.$store.commit('partnerActions/setFormCreateError', {
                    name: 'comeUsCheckBox',
                    step: 'four',
                    value: false
                })
            },
            setGoLink(value) {
                this.$store.commit('partnerActions/setFormCreateField', {
                    name: 'goLink',
                    value: value,
                    step: 'four',
                })
                this.$store.commit('partnerActions/setFormCreateField', {
                    name: 'goLinkCheckBox',
                    value: true,
                    step: 'four',
                })
                this.$store.commit('partnerActions/setFormCreateError', {
                    name: 'callUsCheckBox',
                    step: 'four',
                    value: false
                })
                this.$store.commit('partnerActions/setFormCreateError', {
                    name: 'comeUsCheckBox',
                    step: 'four',
                    value: false
                })
            },
            setComeUs(value) {
                this.$store.commit('partnerActions/setFormCreateField', {
                    name: 'comeUs',
                    value: value,
                    step: 'four',
                })
                this.$store.commit('partnerActions/setFormCreateField', {
                    name: 'comeUsCheckBox',
                    value: true,
                    step: 'four',
                })
                this.$store.commit('partnerActions/setFormCreateError', {
                    name: 'callUsCheckBox',
                    step: 'four',
                    value: false
                })
                this.$store.commit('partnerActions/setFormCreateError', {
                    name: 'comeUsCheckBox',
                    step: 'four',
                    value: false
                })
            },
            removeError(field, step) {
                this.$store.commit('partnerActions/setFormCreateError', {name: field, step: step, value: false})
            },

            fieldBlur(field, step) {
                this.checkStep(step)
                this.$store.commit('partnerActions/setFormCreateFocus', {name: field, step: step, value: false})
            },
            fieldFocus(field, step) {
                this.removeError(field, step)
                this.$store.commit('partnerActions/setFormCreateFocus', {name: field, step: step, value: true})
                if(field === 'discount'){
                    this.$store.commit('partnerActions/setFormCreateError', {name: 'onenessDiscount', step: step, value: false});
                    this.$store.commit('partnerActions/setFormCreateError', {name: 'originDiscount', step: step, value: false});
                }
                this.$store.commit('partnerActions/setFormCreateBtnHide', {
                    name: 'oneness',
                    value: false,
                    step: 'one',
                });
                this.$store.commit('partnerActions/setFormCreateBtnClick', {
                    name: 'oneness',
                    value: 0,
                    step: 'one',
                });
                this.$store.commit('partnerActions/setFormCreateBtnHide', {
                    name: 'origin',
                    value: false,
                    step: 'one',
                });
                this.$store.commit('partnerActions/setFormCreateBtnClick', {
                    name: 'origin',
                    value: 0,
                    step: 'one',
                });

            },
            inputText(field, step, value) {
                this.$store.commit('partnerActions/setFormCreateField', {
                    name: field,
                    value: value,
                    step: step,
                })
            },

            openOriginDiscount: function () {
                let self = this;
                if (this.stepsCreateAction.one.buttons.origin.hide === false) {
                    if (this.stepsCreateAction.one.buttons.origin.state !== 'disable') {
                        this.$store.dispatch('popups/open', {name: this.popups.originDiscount.name});
                    } else {
                        /*if (this.stepsCreateAction.one.buttons.origin.clicks < 2) {
                            this.$store.commit('partnerActions/setFormCreateBtnClick', {
                                name: 'origin',
                                value: this.stepsCreateAction.one.buttons.origin.clicks + 1,
                                step: 'one',
                            })
                        } else {*/
                            this.$store.commit('partnerActions/setFormCreateBtnHide', {
                                name: 'origin',
                                value: true,
                                step: 'one',
                            })
                            setTimeout(function () {
                                self.$store.commit('partnerActions/setFormCreateBtnHide', {
                                    name: 'origin',
                                    value: false,
                                    step: 'one',
                                })
                                self.$store.commit('partnerActions/setFormCreateBtnClick', {
                                    name: 'origin',
                                    value: 0,
                                    step: 'one',
                                })
                            }, 2500);

                        //}
                    }
                }


            },
            openConfirmDiscount: function () {
                let self = this;
                if (this.stepsCreateAction.one.buttons.oneness.hide === false) {
                    if (this.stepsCreateAction.one.buttons.oneness.state !== 'disable') {
                        this.$store.dispatch('popups/open', {name: this.popups.confirmDiscount.name});
                    } else {
                        if (this.stepsCreateAction.one.buttons.oneness.clicks < 2) {
                            this.$store.commit('partnerActions/setFormCreateBtnClick', {
                                name: 'oneness',
                                value: this.stepsCreateAction.one.buttons.oneness.clicks + 1,
                                step: 'one',
                            })
                        } else {
                            this.$store.commit('partnerActions/setFormCreateBtnHide', {
                                name: 'oneness',
                                value: true,
                                step: 'one',
                            })
                            setTimeout(function () {
                                self.$store.commit('partnerActions/setFormCreateBtnHide', {
                                    name: 'oneness',
                                    value: false,
                                    step: 'one',
                                })
                                self.$store.commit('partnerActions/setFormCreateBtnClick', {
                                    name: 'oneness',
                                    value: 0,
                                    step: 'one',
                                })
                            }, 2500);

                        }

                    }
                }

            },
            prevConfirmDiscount() {
                this.$store.commit('popups/close', {name: this.popups.correctText.name});
                this.$store.commit('popups/close', {name: this.popups.correctTextNoDiscount.name});
                this.$store.dispatch('popups/open', {name: this.popups.confirmDiscount.name});
            },
            hideConfirmDiscount() {
                this.$store.commit('popups/close', {name: this.popups.correctText.name});
                this.$store.commit('popups/close', {name: this.popups.correctTextNoDiscount.name});
            },
            nextStep() {
                if (this.currentStep !== 'finish') {
                    this.$store.dispatch('partnerActions/nextStep');
                } else {
                    this.buttons.save.state = 'load';
                    let id,
                        codes = Object.getOwnPropertyNames(this.statuses);

                    if (this.$route.params.id === 'new') {
                        id = this.tmpId;
                    } else {
                        id = this.$route.params.id;

                    }
                    this.$store.dispatch('partnerActions/saveAction', {
                        id: this.$route.params.id,
                        isModerator: this.isModerator
                    }).then(() => {
                        this.buttons.save.state = 'ok';
                        setTimeout(() => {
                            let code = codes[0];

                            /*if (this.$route.params.id !== 'new') {

                                for(let c in this.statuses) {
                                    if (this.detail.status === this.statuses[c]) {
                                        code = c;
                                        break;
                                    }
                                }

                            }*/

                            if (this.isModerator) {
                                this.$router.push('/moderator/coupons/' + code + '/id' + id);
                            } else {
                                this.$router.push('/partner/coupons/' + code + '/id' + id);
                            }
                            this.buttons.save.state = 'normal';
                        }, 300);
                    }, () => {
                        this.buttons.save.state = 'normal';
                    })
                }

            },
            originDiscountChange(value) {
                this.$store.commit('partnerActions/setFormCreateField', {
                    name: 'originDiscount',
                    value: value,
                    step: 'one',
                })
            },
            openConstructActionCallUs: function () {
                this.$store.dispatch('popups/open', {name: this.popups.constructActionCallUs.name});
            },
            openConstructActionComeUs: function () {
                this.$store.dispatch('popups/open', {name: this.popups.constructActionComeUs.name});
            },
            openConstructActionGoLink: function () {
                this.$store.dispatch('popups/open', {name: this.popups.constructActionGoLink.name});
            },
            delCity: function (city) {
                this.$store.commit('partnerActions/delCity', {
                    city: city,
                })
                if (city === 'all') {
                    this.$store.commit('partnerActions/setFormCreateField', {
                        name: 'cities',
                        value: [this.cityPartner],
                        step: 'five',
                    })
                }
                this.changeCitySelect()
            },
            openConstructActionAllCity: function () {
                this.$store.dispatch('popups/open', {name: this.popups.constructActionAllCity.name});
            },
            openConstructActionCity: function () {
                this.$store.dispatch('popups/open', {name: this.popups.constructActionCity.name});
            },
            getCityByXmlId(xmlId) {
                return this.$store.getters['cities/getByXmlId'](xmlId);
            },
            cityChange: function (data) {
                let cities = [];
                for (let r in data.regions) {
                    for (let c in data.regions[r].cities) {
                        if (data.regions[r].cities[c].isChecked) {
                            cities.push({...data.regions[r].cities[c]});
                        }
                    }
                }
                this.removeError('cities', 'five')
                this.$store.commit('partnerActions/setFormCreateField', {
                    name: 'cities',
                    value: cities,
                    step: 'five',
                })
            },
            changeCitySelect: function (bReset) {
                if (this.cities.size > 0) {
                    let citySelectOptions = {};
                    let count = 0;
                    let cities = [...this.cities];
                    for (let cityIndex in cities) {
                        if (cities[cityIndex].regionCode && !citySelectOptions.hasOwnProperty(cities[cityIndex].regionCode)) {
                            let mainIndex = 0;
                            if(cities[cityIndex].region === ' '){
                                mainIndex = 2;
                            }
                            if(cities[cityIndex].region === ' '){
                                mainIndex = 1;
                            }
                            citySelectOptions[cities[cityIndex].regionCode] = {
                                name: cities[cityIndex].region,
                                id: cities[cityIndex].regionCode,
                                isOpen: false,
                                isChecked: 0,
                                cities: [],
                                isMain: mainIndex,
                                isDelimiter: cities[cityIndex].region === ' '
                            }
                        }
                        if (cities[cityIndex].regionCode) {
                            let isChecked = this.stepsCreateAction.five.fields.cities.value.find(city => city.xmlId === cities[cityIndex].xmlId);
                            citySelectOptions[cities[cityIndex].regionCode].cities.push({
                                isChecked: isChecked !== undefined,
                                xmlId: cities[cityIndex].xmlId,
                                name: cities[cityIndex].name,
                                isCapital: cities[cityIndex].isCapital,
                            });
                            if (isChecked !== undefined) {
                                //citySelectOptions[cities[cityIndex].regionCode].isOpen = true;
                                count++;
                            }
                        }
                    }
                    for (let index in citySelectOptions) {
                        let sortable = [];
                        for (let city in citySelectOptions[index].cities) {
                            sortable.push(citySelectOptions[index].cities[city]);
                        }

                        sortable.sort(function (a, b) {
                            if (a.name > b.name) {
                                return 1;
                            }
                            if (a.name < b.name) {
                                return -1;
                            }
                            if (a.name == b.name) {
                                return 0;
                            }
                        });
                        sortable.sort(function (a, b) {
                            if (a.isCapital < b.isCapital) {
                                return 1;
                            }
                            if (a.isCapital > b.isCapital) {
                                return -1;
                            }
                            if (a.isCapital == b.isCapital) {
                                return 0;
                            }
                        });
                        citySelectOptions[index].cities = sortable;
                    }
                    let sortable = [];
                    for (let index in citySelectOptions) {
                        sortable.push(citySelectOptions[index]);
                    }
                    sortable.sort(function (a, b) {
                        if (a.name > b.name) {
                            return 1;
                        }
                        if (a.name < b.name) {
                            return -1;
                        }
                        if (a.name == b.name) {
                            return 0;
                        }
                    });
                    sortable.sort(function (a, b) {
                        if (a.isMain < b.isMain) {
                            return 1;
                        }
                        if (a.isMain > b.isMain) {
                            return -1;
                        }
                        if (a.isMain == b.isMain) {
                            return 0;
                        }
                    });
                    this.citySelectOptions = sortable;
                }
            },
            checkStep(step) {
                if (step !== this.currentStep && this.currentStep !== 'one') {
                    this.$store.dispatch('partnerActions/checkStep', {step: step})
                }
            },
            pageUp() {
                if (this.$route.params.id !== 'new') {
                    window.scroll(0, 0);
                }
            },

            openVideoAdd: function () {
                this.$store.dispatch('popups/open', {name: this.popups.constructVideoAdd.name});
            },

            number_format(result) {
                return number_format(result, 0, '', ' ')
            },
            closePartnerModerateErrorPercent: function () {
                this.$store.commit('popups/close', {name: this.popups.partnerModerateErrorPercent.name});
                this.setFocus('projectDiscount')
            },
            closePartnerModerateErrorSum: function () {
                this.$store.commit('popups/close', {name: this.popups.partnerModerateErrorSum.name});
            },
            closePartnerModerateErrorProfit: function () {
                this.$store.commit('popups/close', {name: this.popups.cooperationCancelSmallPriceOne.name});
            },
            setFocus: function(ref) {
                if(!!this.$refs[ref]){
                    let input = this.$refs[ref].$el.getElementsByClassName('inp-s')[0];
                    if(input){

                        input.focus()
                        setTimeout(function () {
                            input.select()
                        },20)
                    }
                }
            },
            confirmDiscountChange(val){
                this.$store.commit('partnerActions/setFormCreateField', {
                    name: 'discountUser',
                    value: val,
                    step: 'one',
                });
            },
        }
    }
</script>

